@import "../../common/colors";
.movie-list{
  margin: 20px 0;
  h2{
    color: $font-secondary;
    margin-bottom: 10px;
    font-weight: 400;
  }
}
.shows-list{
  margin: 20px 0;
  h2{
    color: $font-secondary;
    margin-bottom: 10px;
    font-weight: 400;
  }
}
.movie-container{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px,1fr));
  grid-gap: 15px;
}
.show-container{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px,1fr));
  grid-gap: 15px;
}