@import "../../common/colors.scss";
.movie-section{
  display: flex;
  justify-content: space-between;
  padding: 40px 0;
  color: $font-primary;
  font-weight: 400;
}
.movie-rating{
  padding-left: 3px;
  margin-top: 20px;
  color: $font-secondary;
display: flex;
  span{
    margin-right: 20px;
    font-size: 1rem;

  }
}
.movie-title{
  font-size: 40px;
  color: $font-primary;
}
.movie-plot{
  margin-top: 20px;
  line-height: 1.8rem;
}
.movie-info > div span:first-child{
  padding: 10px 0;
  color: $font-primary;
  font-weight: 600;
  width: 100px;
  display: inline-block;
}
.movie-info > div span{
  color: $font-secondary;
}
.fa-star,fa-thumbs-up,fa-film,fa-calendar{
  color: #ff9e00;
}
.section-fight{
  margin-left: 20px;
}